import Evento from "./Evento";

const Eventos = (props) => {
    console.log(props);
    const eventosId = Object.keys(props.Events);
    
    return ( 
        <div className="list-group">
            {eventosId.map(key =>(
                <Evento
                    key={key}
                    info={props.eventos[key]}
                    />
            ))}
        </div>
     );
}
 
export default Eventos;